
.container{
 
    width: 350px;
    margin: auto;
    text-align: center;
    padding: 20px;
    margin-top: 20px;
    border: 1px solid #d9d6d6;
    border-radius: 5px;
    padding-bottom: 30px;
    box-shadow: 0px 0px 5px 0px #000000;
}



input[type='text']{
  padding: 6px;
  border: 1px solid #c2c3c5;
  outline: none;
  border-radius: 5px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 70%;
}

input[type='text']:focus{
  box-shadow: 0px 0px 5px 0px #000000;
}

.container .button1{
  padding: 4px 10px;
  font-size: 20px;
  border-radius: 8px;
  background-color: #3a6ebd;
  color: #ffffff;
  border: none;
  vertical-align: bottom;
  cursor: pointer;
  margin-left: 5px;
  border: 1px solid gray;
}

.container .button1:hover{

  background-color: #2cb98b;
}

.container li{
    text-align: left;
    list-style: none;
}

.container ul{
    padding-left: 22px;
}

.overline{
    text-decoration-line: line-through;
    opacity: .6;
    
}

.noneline{
    text-decoration-line: none;
    opacity: 1;
}

